<template>
        <form>
            <v-text-field
            class=""
                v-model="$store.state.project.name"
                label="Project Name"
                required
                variant="underlined"
            ></v-text-field>
            <v-row>
                <v-col cols="9">
                    <v-text-field
                        class=""
                        v-model="$store.state.project.directory"
                        label="Project Directory"
                        required
                        variant="underlined"
                        readonly
                    ></v-text-field>
                </v-col>
                <v-col cols="3"><v-btn block @click="$API.send('select-directory')" color="blue-darken-2">Browse</v-btn><br></v-col>   
            </v-row>
            <div class="text-center">
                <v-btn color="blue-darken-2" @click="createProject" class="mt-3" :disabled="$store.state.project.directory==''||$store.state.project.name==null">Create Project</v-btn>
            </div>
        </form>
</template>
<script>
    export default{
        name:'ChooseDirectory',
        data(){
            return {}
        },
        async mounted(){
            this.$API.receive('select-directory', data=>{
                this.$store.state.project.directory = data.directory;
            })
            this.$API.receive('create-project', data=>{
                this.$store.state.project.directory = data.directory;
            })
        },
        methods:{
            createProject(){
                if(!this.$API.existsSync('.nsmkr')){
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/MAPS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/SCREENS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/TILESETS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/OBJECTS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/ANIMATIONS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/SOUNDS`, {recursive:true});
                    this.$API.mkdirSync(`${this.$store.state.project.directory}/CODE`, {recursive:true});
                    this.$API.writeFileSync(`${this.$store.state.project.directory}/.nsmkr`, JSON.stringify(this.$store.state.project));
                }
                else{
                    
                }
            }
        },
    }
</script>
<style scoped lang="scss">
    img{
        width: 220px;
        height: 180px;
        display:block;
        margin-left: auto;
        margin-right: auto;
        object-fit:scale-down;
        object-position: 50% 50%;
    }
</style>