<template>
  <RetroverseBar />
  <v-navigation-drawer permanent>
    <div class="text-center">
      <div class="mt-4 mb-2">
          <v-btn class="bg-light-blue-darken-3 pill-button" @click.prevent.stop="create=!create; createProject()">Create</v-btn>

          <v-dialog v-model="create" width="680" persistent class="">
            <v-card class="">
                <v-card-text>
                    <div class=""></div>
                    <ChooseSystem v-if="$store.state.state=='Choose System'" />
                    <ChooseSetup v-if="$store.state.state=='Choose Setup'" />
                    <ChooseGenre v-if="$store.state.state=='Choose Genre'" />
                    <AdvancedSetup v-if="$store.state.state=='Advanced Setup'" />
                    <!-- <ChooseDirectory v-if="$store.state.project != null&&$store.state.project.system!=null" />
                    <ChooseSetup v-if="$store.state.project != null&&$store.state.project.system!=null&&$store.state.project.setup==null" /> -->
                </v-card-text>
                <v-card-actions class="d-flex justify-center text-center">
                    <v-btn @click="create = false; $store.state.project=null">Cancel</v-btn>
                </v-card-actions>
            </v-card>
          </v-dialog>

      </div>
      <div class="mt-2 mb-4">
          <v-btn class="bg-light-blue-darken-3 pill-button" @click.prevent.stop="openProject">Open</v-btn>
      </div>
    </div>
    </v-navigation-drawer>
  <div class="h-100  bg-grey-lighten-4 pt-10 pb-10 ps-10 pe-10">
    <div v-html="html" v-if="html"></div>
    <div class="text-center  h-100 d-flex align-center justify-center" v-else><v-progress-circular  size="50" indeterminate color="blue-lighten-1"></v-progress-circular></div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

// Components
import RetroverseBar from '@/components/RetroverseBar.vue';
import RecentProjects from '@/components/RecentProjects.vue';
import ChooseSystem from '@/components/CreateOpen/ChooseSystem.vue';
import ChooseDirectory from '@/components/CreateOpen/ChooseDirectory.vue';
import ChooseSetup from '@/components/CreateOpen/ChooseSetup.vue';
import ChooseGenre from '@/components/CreateOpen/ChooseGenre.vue';
import AdvancedSetup from '@/components/CreateOpen/AdvancedSetup.vue';
export default defineComponent({
  name: 'HomeView',

  components: {
    RetroverseBar,
    RecentProjects,
    ChooseSystem,
    ChooseSetup,
    ChooseGenre,
    ChooseDirectory,
    AdvancedSetup
  },
  data(){
    return {
      html:'',
      create:false
    }
  },
  async mounted(){
    let req = await this.$axios.get('https://theretroverse.com/wp-json/wp/v2/pages/2084');
    this.html = req.data.content.rendered;
  },
  methods:{
    createProject(){
        this.$store.state.project = {
            system:null,
            name:'',
            directory:'',
            setup:null,
            genre:null,
            assember:'ASM6'
        };
        this.$store.state.state = 'Choose System';
    },
    openProject(){
      console.log(this.$API.showOpenDialogSync)
      let res = this.$API.showOpenDialogSync({
        buttonLabel:'Select Project', 
        title:'Select a Project',
        properties: ['openDirectory', 'createDirectory', 'showHiddenFiles'],
        filters:[
          {name:'NES Maker', extensions:['nsmkr']}
        ]
      });
    },
  }
});
</script>
