<template>
    <v-navigation-drawer :rail="rail" permanent theme="dark">
        <v-list-item prepend-avatar="https://theretroverse.com/wp-content/uploads/2022/10/cropped-Favicon-1-32x32.png" title="Retroverse" subtitle="Not logged in" @click="rail=!rail" nav>
          <template v-slot:append>
            <v-btn variant="text" icon="mdi-chevron-left" @click.stop="rail = !rail"></v-btn>
          </template>
        </v-list-item>

        <v-divider></v-divider>

        <v-list density="compact" nav>
          <v-list-item prepend-icon="mdi-login" title="Log-In" value="log-in" @click="login=!login"></v-list-item>
        </v-list>

        <v-dialog v-model="login" width="670" theme="light">
          <v-card>
            <v-card-title>
              <div class="text-h5 text-center d-block pb-4">Retroverse Login</div>
            </v-card-title>
            <v-card-text>
              <v-alert v-if="error" v-html="error" type="warning" prominent class="mb-5"></v-alert>

              <form @submit.prevent.stop="logIn">
                <fieldset :disabled="disabled">
                  <v-text-field v-model="usrnm" label="Email Address *" required variant="underlined" ></v-text-field>
                  <v-text-field v-model="pswd" label="Password *" required type="password" variant="underlined"></v-text-field>
                  <div class="text-end">
                    <v-btn color="blue-darken-2" type="submit">
                      <span v-if="!disabled">Log-In</span>
                      <v-progress-circular v-else size="24" indeterminate color="blue-lighten-5"></v-progress-circular> 
                    </v-btn>
                  </div>
                </fieldset>
              </form>

            </v-card-text>
          </v-card>
        </v-dialog>
    </v-navigation-drawer>
</template>
<script>
    export default{
        name:'NotLoggedIn',
        props:['rail'],
        data(){
            return {
                login:false,
                usrnm:'',
                pswd:'',
                error:'',
                disabled:false
            }
        },
        async mounted(){},
        methods:{
            async logIn(){
                this.error='';
                this.disabled = true;
                // try{
                    let res = await this.$axios.post(`${this.$store.state.retroverseURL}/wp-json/jwt-auth/v1/token`, {username:this.usrnm, password: this.pswd});
                    if(res.data.token!=undefined){
                        this.$store.state.token = res.data.token;
                        localStorage.token = res.data.token;

                        res = await this.$axios.get(`${this.$store.state.retroverseAPI}/users/me`, {
                        headers:{
                            'Authorization':`Bearer ${this.$store.state.token}`
                        }
                        });
                        localStorage.setItem('retroverseUser', JSON.stringify(res.data));
                        this.$store.state.retroverseUser = res.data;
                    }
                    else{
                    this.error = 'Incorrect username or password.';
                    }
                this.disabled = false;

                },
        }
    }
</script>