import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import "@mdi/font/css/materialdesignicons.css";

import { loadFonts } from './plugins/webfontloader'
import _ from 'lodash'
import axios from'axios'
loadFonts()

const app = createApp({
  ...App,
  ...{
    icons:{
      iconfont:'mdi'
    },
    data(){
      return {
      }
    },
    async mounted(){
      this.$store.state.db.version(1).stores({
        recentProjects:'++id, directory, name, createDate, lastChangeDate'
      });
      this.$store.state.recentProjects = await this.$store.state.db.recentProjects.toArray();

    }
  }
})
  .use(router)
  .use(store)
  .use(vuetify);
  app.config.globalProperties.$_ = _;
  app.config.globalProperties.$axios = axios;
  app.config.globalProperties.$API = window.API;
  app.mount('#app')